.resize-handle {
  position: absolute;
  width: 8px;
  height: 8px;
  z-index: 88888;
  pointer-events: auto; }
  .resize-handle.top_left {
    top: -8px;
    left: -8px;
    cursor: nw-resize; }
    .resize-handle.top_left:after {
      content: "";
      position: absolute;
      width: 8px;
      height: 8px;
      background: #000;
      top: 4px;
      left: 4px; }
  .resize-handle.top {
    top: -8px;
    left: 50%;
    margin-left: -8px;
    cursor: n-resize; }
    .resize-handle.top:after {
      content: "";
      position: absolute;
      width: 8px;
      height: 8px;
      background: #000;
      top: 4px;
      left: 4px; }
  .resize-handle.top_right {
    top: -8px;
    right: -8px;
    cursor: ne-resize; }
    .resize-handle.top_right:after {
      content: "";
      position: absolute;
      width: 8px;
      height: 8px;
      background: #000;
      top: 4px;
      left: -4px; }
  .resize-handle.right {
    top: 50%;
    right: -8px;
    margin-top: -8px;
    cursor: e-resize; }
    .resize-handle.right:after {
      content: "";
      position: absolute;
      width: 8px;
      height: 8px;
      background: #000;
      top: 4px;
      left: -4px; }
  .resize-handle.bottom_right {
    bottom: -8px;
    right: -8px;
    cursor: se-resize; }
    .resize-handle.bottom_right:after {
      content: "";
      position: absolute;
      width: 8px;
      height: 8px;
      background: #000;
      top: -4px;
      left: -4px; }
  .resize-handle.bottom {
    bottom: -8px;
    left: 50%;
    margin-left: -8px;
    cursor: s-resize; }
    .resize-handle.bottom:after {
      content: "";
      position: absolute;
      width: 8px;
      height: 8px;
      background: #000;
      top: -4px;
      left: 4px; }
  .resize-handle.bottom_left {
    bottom: -8px;
    left: -8px;
    cursor: sw-resize; }
    .resize-handle.bottom_left:after {
      content: "";
      position: absolute;
      width: 8px;
      height: 8px;
      background: #000;
      top: -4px;
      left: 4px; }
  .resize-handle.left {
    top: 50%;
    margin-top: -8px;
    left: -8px;
    cursor: w-resize; }
    .resize-handle.left:after {
      content: "";
      position: absolute;
      width: 8px;
      height: 8px;
      background: #000;
      top: 4px;
      left: 4px; }

.container-card {
  width: 300px;
  height: 300px;
  margin: 10px;
  box-sizing: border-box;
  padding: 10px;
  background: #ffffff; }

#div1 {
  position: relative;
  width: 450px;
  height: 450px;
  margin: 20px auto 0; }

#div1 a {
  position: absolute;
  top: 0px;
  left: 0px;
  font-family: Microsoft YaHei;
  color: #0b85ff;
  font-weight: bold;
  text-decoration: none;
  padding: 3px 6px; }

#div1 .blue {
  color: blue; }

#div1 .red {
  color: red; }

#div1 .yellow {
  color: yellow; }

p {
  font: 16px Microsoft YaHei;
  text-align: center;
  color: #ba0c0c; }

p a {
  font-size: 14px;
  color: #ba0c0c; }

p a:hover {
  color: red; }

@-webkit-keyframes roll {
  form {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes roll {
  form {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.shuiqiu {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  border: 5px solid #607d8b;
  position: relative;
  overflow: hidden; }

.shuiqiucontent {
  width: calc(100% - 6px);
  height: calc(100% - 6px);
  position: relative;
  overflow: hidden;
  margin: 3px;
  border-radius: 50%; }

.shui {
  position: absolute;
  width: 1000px;
  height: 1000px;
  top: 100px;
  left: 50%;
  margin-left: -500px; }

.shuiqiu:hover .shui1 {
  -webkit-animation: roll 5s linear infinite;
          animation: roll 5s linear infinite; }

.shuiqiu:hover .shui2 {
  -webkit-animation: roll 7s linear infinite;
          animation: roll 7s linear infinite; }

.shuiqiu:hover .shui3 {
  -webkit-animation: roll 6s linear infinite;
          animation: roll 6s linear infinite; }

.shuiqiu:hover .shui4 {
  -webkit-animation: roll 10s linear infinite;
          animation: roll 10s linear infinite; }

.shuiqiu:hover .shui5 {
  -webkit-animation: roll 8s linear infinite;
          animation: roll 8s linear infinite; }

.shuiqiu:hover .shui6 {
  -webkit-animation: roll 9.5s linear infinite;
          animation: roll 9.5s linear infinite; }

.shui1 {
  border-radius: 45%;
  background: #607d8bbe;
  -webkit-animation: roll 10s linear infinite;
          animation: roll 10s linear infinite; }

.shui2 {
  border-radius: 46%;
  background: #607d8b9a;
  -webkit-animation: roll 14s linear infinite;
          animation: roll 14s linear infinite; }

.shui3 {
  border-radius: 47%;
  background: #607d8bab;
  -webkit-animation: roll 12s linear infinite;
          animation: roll 12s linear infinite; }

.shui4 {
  border-radius: 48%;
  background: #607d8b44;
  -webkit-animation: roll 20s linear infinite;
          animation: roll 20s linear infinite; }

.shui5 {
  border-radius: 49%;
  background: #607d8b9f;
  -webkit-animation: roll 16s linear infinite;
          animation: roll 16s linear infinite; }

.shui6 {
  border-radius: 50%;
  background: #607d8b38;
  -webkit-animation: roll 19s linear infinite;
          animation: roll 19s linear infinite; }

